*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html{
  font-size: 12px;
}

body{
  min-height: 100vh;
  font-family:'Times New Roman', Times, serif ;
}

form{
  width: 100%;
  position: fixed;
}

button{
  width: 33.33%;
  padding: 0.5rem;
  font-size: 1rem;
}

button.Selected{
  background-color: black;
  color: white;
}

ul{
  padding: 3rem 2rem 1rem;
}

li{
  margin-bottom: 1rem;
}

.table-container{
  width: 100%;
  overflow-y: auto;
  padding-top: 52px;
}

td{
  border: 1px solid black;
  padding: 0.25rem;
}